<template>
  <div v-if="showEditor" class="btn-group margin-top-10" role="group" aria-label="Basic example">
      <button type="button" class="btn btn-secondary black-border selected" >Editor</button>
      <button type="button" class="btn btn-secondary black-border " @click="showEditor = ! showEditor" >Saved Labels ({{savedLabel.length}})</button>
  </div>
  <div v-else class="btn-group margin-top-10" role="group" aria-label="Basic example">
      <button type="button" class="btn btn-secondary black-border " @click="showEditor = ! showEditor">Editor</button>
      <button type="button" class="btn btn-secondary black-border selected">Saved Labels ({{savedLabel.length}})</button>
  </div>
  <br>
  <div class="vertical-center">
    <transition name="slide-fade" mode="out-in">
      <div  v-if="showEditor" class="row flex-column-reverse flex-lg-row m-0">
        <div class="d-flex justify-content-center">
          <div id="fileDropBox" @dragover="dragover" @dragleave="dragleave" @drop="drop">
            <input type="file" multiple name="fields[assetsFieldHandle][]" id="assetsFieldHandle" class="invisibleInput" @change="onSendCSV" ref="file" accept=".csv" />

            <label for="assetsFieldHandle" class="labelInput">
              <div>
                Drop CSV file here or click <span class="underline">here</span>
              </div>
            </label>
          </div>
        </div>

        <div class="col-lg-8 col-12">
          <div class="row justify-content-center">
            <div class="col-11 col-xl-11  border-lg rounded-lg">
            <h1>Infos</h1>
              <div class="row">
                <div class="col-6 col-lg-6">
                  <div class="input-group mb-3">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="basic-addon1">Year</span>
                    </div>
                    <input type="text" class="form-control" placeholder="2021" aria-label="Année" aria-describedby="basic-addon1"  v-model="etiquette['year']" v-on:input="changeText(etiquette['year'],writeCanvasDict['year'],clearCanvasDict['year'])">
                    <div class="input-group-text">
                      <input v-model="labelCheckbox['year']" v-bind:checked="labelCheckbox['year']" type="checkbox" aria-label="Save input for next label">
                    </div>
                  </div>
                </div>
                <div class="col-6 col-lg-6">
                  <div class="input-group mb-3 v">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="basic-addon1">Ref</span>
                    </div>
                    <input type="text" class="form-control" placeholder="Reference" aria-label="Ref" aria-describedby="basic-addon1"  v-model="etiquette['reference']" v-on:input="changeText(etiquette['reference'],writeCanvasDict['reference'],clearCanvasDict['reference'])">
                    <div class="input-group-text">
                      <input v-model="labelCheckbox['reference']" v-bind:checked="labelCheckbox['reference']" type="checkbox" aria-label="Save input for next label">
                    </div>
                  </div>
                </div>
              </div>

              <div class="input-group mb-3 mx-auto">
                <div class="input-group-prepend">
                  <span class="input-group-text" id="basic-addon1">Product Name</span>
                </div>
                <input type="text" class="form-control" placeholder="Product Name" aria-label="Product Name" aria-describedby="basic-addon1"  v-model="etiquette['productName']" v-on:input="changeText(etiquette['productName'],writeCanvasDict['productName'],clearCanvasDict['productName'])">
                <div class="input-group-text">
                  <input v-model="labelCheckbox['productName']" v-bind:checked="labelCheckbox['productName']" type="checkbox" aria-label="Save input for next label">
                </div>
              </div>
              <div class="input-group mb-3">
                <div class="input-group-prepend">
                  <span class="input-group-text" id="basic-addon1">Description</span>
                </div>
                <input type="text" class="form-control" placeholder="Description" aria-label="Description" aria-describedby="basic-addon1"  v-model="etiquette['description']" v-on:input="changeText(etiquette['description'],writeCanvasDict['description'],clearCanvasDict['description'])">
                <div class="input-group-text">
                  <input v-model="labelCheckbox['description']" v-bind:checked="labelCheckbox['description']" type="checkbox" aria-label="Save input for next label">
                </div>
              </div>
              <div v-if="etiquette['labelTheme'] == 'old'" class="input-group mb-3">
                <div class="input-group-prepend">
                  <span class="input-group-text" id="basic-addon1">Second Desc</span>
                </div>
                <input type="text" class="form-control" placeholder="Second Desc" aria-label="Seconde Desc" aria-describedby="basic-addon1"  v-model="etiquette['thirdDescription']" v-on:input="changeText(etiquette['thirdDescription'],writeCanvasDict['thirdDescription'],clearCanvasDict['thirdDescription'])">
                <div class="input-group-text">
                  <input v-model="labelCheckbox['thirdDescription']" v-bind:checked="labelCheckbox['thirdDescription']" type="checkbox" aria-label="Save input for next label">
                </div>
              </div>
              <div v-if="etiquette['labelTheme'] == 'old'" class="input-group mb-3">
                <div class="input-group-prepend">
                  <span class="input-group-text" id="basic-addon1">Third Desc</span>
                </div>
                <input type="text" class="form-control" placeholder="Third Description" aria-label="Third Description" aria-describedby="basic-addon1"  v-model="etiquette['secondDescription']" v-on:input="changeText(etiquette['secondDescription'],writeCanvasDict['secondDescription'],clearCanvasDict['secondDescription'])">
                <div class="input-group-text">
                  <input v-model="labelCheckbox['secondDescription']" v-bind:checked="labelCheckbox['secondDescription']" type="checkbox" aria-label="Save input for next label">
                </div>
              </div>
              <div class="input-group mb-3">
                <div class="input-group-prepend">
                  <span class="input-group-text" id="basic-addon1">Price</span>
                </div>
                <input type="text" class="form-control" placeholder="Price" aria-label="Prix" aria-describedby="basic-addon1"  v-model="etiquette['price']" v-on:input="changeText(etiquette['price'],writeCanvasDict['price'],clearCanvasDict['price'])">

                <select class="form-select" aria-label="Select Currency" v-model="etiquette['currency']" v-on:change="changeText(etiquette['currency'],writeCanvasDict['currency'],clearCanvasDict['currency'])">
                  <option v-for="currency in availableCurrencies" v-bind:value="currency" v-bind:key="currency">{{currency}}</option>
                </select>
                <div class="input-group-text">
                  <input v-model="labelCheckbox['price']" v-bind:checked="labelCheckbox['price']" type="checkbox" aria-label="Save input for next label">
                </div>
              </div>

              <div class="input-group mb-3 v">
                <div class="input-group-prepend">
                  <span class="input-group-text" id="basic-addon1">Taxes</span>
                </div>
                <input type="text" class="form-control" placeholder="Taxes" aria-label="TTC" aria-describedby="basic-addon1"  v-model="etiquette['topRightSquare']" v-on:input="changeText(etiquette['topRightSquare'],writeCanvasDict['topRightSquare'],clearCanvasDict['topRightSquare'])">
                <div class="input-group-text">
                  <input v-model="labelCheckbox['topRightSquare']" v-bind:checked="labelCheckbox['topRightSquare']" type="checkbox" aria-label="Save input for next label">
                </div>
              </div>
              <div class="input-group mb-3 v">
                <div class="input-group-prepend">
                  <span class="input-group-text" id="basic-addon1">Type</span>
                </div>
                <input type="text" class="form-control" placeholder="M²" aria-label="M²" aria-describedby="basic-addon1"  v-model="etiquette['bottomRightSquare']" v-on:input="changeText(etiquette['bottomRightSquare'],writeCanvasDict['bottomRightSquare'],clearCanvasDict['bottomRightSquare'])">
                <div class="input-group-text">
                  <input v-model="labelCheckbox['bottomRightSquare']" v-bind:checked="labelCheckbox['bottomRightSquare']" type="checkbox" aria-label="Save input for next label">
                </div>
              </div>
              <button type="button" class="btn btn-success col-md-3 col-lg-3 margin-left" @click="nextLabel">Next Label</button>
              <button v-if="savedLabel.length !=0" type="button" class="btn btn-success col-md-3 col-lg-3 margin-left" @click="saveCanvas">Save PDF</button>

            </div>
          </div>

        </div>

        <div class="col-12 col-lg-4">
          <h1>My Label</h1>
          <div class="row justify-content-center">
            <div class="col-11 col-lg-12">
              <CanvasComponent canvasID="canvas" v-bind:index=-1 ></CanvasComponent>

              <div class="row">
                <div class="col">
                  <span class="colorSpan" id="basic-addon1">Main Color: </span>
                  <color-input disable-alpha class="border rounded" v-model="etiquette['mainColor']" @change="redraw('canvas',-1)"/>
                </div>
                <div class="col">
                  <span class="colorSpan" id="basic-addon1">Secondary Color: </span>
                  <color-input disable-alpha class="border rounded" v-model="etiquette['secondColor']" @change="redraw('canvas',-1)"/>
                </div>
              </div>

              <div class="input-group mb-3">
                <div class="input-group-prepend">
                  <span class="input-group-text" id="basic-addon1">Change Theme</span>
                </div>
                <select class="form-select" aria-label="Changer le thème" v-model="etiquette['labelTheme']" v-on:change="changeCanvas">
                  <option v-for="theme in availableThemes" v-bind:value="theme.value" v-bind:key="theme">{{theme.name}}</option>
                </select>
              </div>    
            </div>
          </div>

        </div>

      </div>
      <div v-else class="row flex-lg-row m-0 flex-column">
        <div class="row justify-content-center m-0">
          <div class="col-11">
            <h1 class="text-start">Created Labels:</h1>
          </div>
        </div>
        <div v-if="savedLabel.length !=0" class="row createdLabels justify-content-center m-0">
            <div class="col-11">
            <div class="row flex-column flex-lg-row">
              <div class="col col-lg-4" v-for="createdLabel,index in savedLabel" v-bind:key="createdLabel && index" >
                <CanvasComponent v-bind:canvasID="'canvas'+index" v-bind:index="index" canvasClass="hover-overlay"></CanvasComponent>
                <div v-bind:id="'expand'+index" class="collapse">
                  <div class="card card-body">
                    
                    
                    <div class="row">
                      <div class="col-6 col-lg-6">
                        <div class="input-group mb-3">
                          <div class="input-group-prepend">
                            <span class="input-group-text" id="basic-addon1">Year</span>
                          </div>
                          <input type="text" class="form-control" placeholder="2021" aria-label="Année" aria-describedby="basic-addon1"  v-model="savedLabel[index]['year']" v-on:input="changeTextNew(savedLabel[index]['year'],writeCanvasDict['year'],clearCanvasDict['year'],'canvas'+index)">
                        </div>
                      </div>
                      <div class="col-6 col-lg-6">
                        <div class="input-group mb-3 v">
                          <div class="input-group-prepend">
                            <span class="input-group-text" id="basic-addon1">Ref</span>
                          </div>
                          <input type="text" class="form-control" placeholder="Reference" aria-label="Ref" aria-describedby="basic-addon1"  v-model="savedLabel[index]['reference']" v-on:input="changeTextNew(savedLabel[index]['reference'],writeCanvasDict['reference'],clearCanvasDict['reference'],'canvas'+index)">
                        </div>
                      </div>
                    </div>

                    <div class="input-group mb-3 mx-auto">
                      <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">Product Name</span>
                      </div>
                      <input type="text" class="form-control" placeholder="Product Name" aria-label="Product Name" aria-describedby="basic-addon1"  v-model="savedLabel[index]['productName']" v-on:input="changeTextNew(savedLabel[index]['productName'],writeCanvasDict['productName'],clearCanvasDict['productName'],'canvas'+index)">
                    </div>
                    <div class="input-group mb-3">
                      <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">Description</span>
                      </div>
                      <input type="text" class="form-control" placeholder="Description" aria-label="Description" aria-describedby="basic-addon1"  v-model="savedLabel[index]['description']" v-on:input="changeTextNew(savedLabel[index]['description'],writeCanvasDict['description'],clearCanvasDict['description'],'canvas'+index)">
                    </div>
                    <div v-if="savedLabel[index]['labelTheme'] == 'old'" class="input-group mb-3">
                      <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">Second Desc</span>
                      </div>
                      <input type="text" class="form-control" placeholder="Seconde Desc" aria-label="Second Desc" aria-describedby="basic-addon1"  v-model="savedLabel[index]['thirdDescription']" v-on:input="changeTextNew(savedLabel[index]['thirdDescription'],writeCanvasDict['thirdDescription'],clearCanvasDict['thirdDescription'],'canvas'+index)">
                    </div>
                    <div v-if="savedLabel[index]['labelTheme'] == 'old'" class="input-group mb-3">
                      <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">Third Desc</span>
                      </div>
                      <input type="text" class="form-control" placeholder="Third Description" aria-label="Troisieme Description" aria-describedby="basic-addon1"  v-model="savedLabel[index]['secondDescription']" v-on:input="changeTextNew(savedLabel[index]['secondDescription'],writeCanvasDict['secondDescription'],clearCanvasDict['secondDescription'],'canvas'+index)">
                    </div>
                    <div class="input-group mb-3">
                      <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">Price</span>
                      </div>
                      <input type="text" class="form-control" placeholder="Prix" aria-label="Prix" aria-describedby="basic-addon1"  v-model="savedLabel[index]['price']" v-on:input="changeTextNew(savedLabel[index]['price'],writeCanvasDict['price'],clearCanvasDict['price'],'canvas'+index)">

                      <select class="form-select" aria-label="Select Currency" v-model="savedLabel[index]['currency']" v-on:change="changeTextNew(savedLabel[index]['currency'],writeCanvasDict['currency'],clearCanvasDict['currency'],'canvas'+index)">
                        <option v-for="currency in availableCurrencies" v-bind:value="currency" v-bind:key="currency">{{currency}}</option>
                      </select>
                    </div>

                    <div class="input-group mb-3 v">
                      <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">Taxes</span>
                      </div>
                      <input type="text" class="form-control" placeholder="TTC" aria-label="TTC" aria-describedby="basic-addon1"  v-model="savedLabel[index]['topRightSquare']" v-on:input="changeTextNew(savedLabel[index]['topRightSquare'],writeCanvasDict['topRightSquare'],clearCanvasDict['topRightSquare'],'canvas'+index)">
                    </div>
                    <div class="input-group mb-3 v">
                      <div class="input-group-prepend">
                        <span class="input-group-text" id="basic-addon1">Type</span>
                      </div>
                      <input type="text" class="form-control" placeholder="M²" aria-label="M²" aria-describedby="basic-addon1"  v-model="savedLabel[index]['bottomRightSquare']" v-on:input="changeTextNew(savedLabel[index]['bottomRightSquare'],writeCanvasDict['bottomRightSquare'],clearCanvasDict['bottomRightSquare'],'canvas'+index)">
                    </div>
                    <button class="btn btn-danger" data-bs-toggle="collapse" type="button" v-bind:data-bs-target="'#expand'+index" aria-expanded="false" v-bind:aria-controls="'expand'+index">Close</button>
                    
                    
                    
                    
                    
                  </div>
                </div>
              
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>

    <!--
    <div v-if="savedLabel.length !=0" class="row createdLabels justify-content-center m-0">
      <div class="col-11">
        <h1 class="text-start">Created Labels:</h1>
      </div>
        <div class="col-11">
        <div class="row flex-column flex-lg-row">
          <div class="col col-lg-4" v-for="createdLabel,index in savedLabel" v-bind:key="createdLabel && index" >
            {{index}}
            <div class="">
              <canvas  class="border rounded canvasWidth" v-bind:id="'canvas'+index" width="588" height="260" v-on:click="drawEditedCanvas(index)"></canvas>
            </div>
          </div>
        </div>
      </div>
    </div>
-->
  </div>

</template>

<script>
import { jsPDF } from "jspdf";
import CanvasComponent from './Canvas.vue'
import Papa from 'papaparse'

//Tmura ML Font
export default {
  name: 'Etiquette',
  components: {
  CanvasComponent
  },
  data: function(){
    return {
      etiquette:{
        labelTheme:"new",
        productName:"",
        reference:"",
        description:"",
        thirdDescription:"",
        secondDescription:"",
        price:"",
        currency:"€",
        topRightSquare:"",
        bottomRightSquare:"",
        year:"2021",
        mainColor:"#112655",
        secondColor:"#FFFFFF"
      },
      showEditor: true,
      labelCheckbox:{
        labelTheme:true,
        productName:false,
        reference:false,
        description:false,
        thirdDescription:false,
        secondDescription:false,
        price:false,
        currency:true,
        topRightSquare:false,
        bottomRightSquare:false,
        year:true,
      },
      availableCurrencies: ["€","$","£"],
      availableThemes:[{name:"Ancienne Etiquette",value:"old",mainColor:"#F0367D",secondColor:"#FFFFFF"},{name:"Nouvelle Etiquette",value:"new",mainColor:"#112655",secondColor:"#FFFFFF"}],
      pink: "#F0367D",
      fontName:"DinProMedium",
      clearCanvasDict:{
      },
      writeCanvasDict:{
    },
      themesColor:{old:{mainColor:"#F0367D",secondColor:"#FFFFFF"},
                  new:{mainColor:"#112655",secondColor:"#FFFFFF"}},
      savedLabel:[]
    }
  },
  mounted (){
    this.drawCanvasNew("canvas",this.etiquette)
    document.onreadystatechange = () => {
      if (document.readyState == "complete") {

        this.drawAllNew("canvas",this.etiquette)
      }
    }
  },
  methods:{
    deleteLabel(index){
      console.log(this.savedLabel[index]["productName"])
      this.savedLabel.splice(index,1)
      for(var i in this.savedLabel){
        this.redraw("canvas"+i,i)
      }
    },
    redraw(canvasName,index){
      if(index==-1){
        this.drawCanvasNew(canvasName,this.etiquette)
        this.drawAllNew(canvasName,this.etiquette)
      }
      else{
        this.drawCanvasNew(canvasName,this.savedLabel[index])
        this.drawAllNew(canvasName,this.savedLabel[index])    
      }
    },
    changeText(name,fontData,clear){
      this.clearCanvas(clear)
      var canvas = document.getElementById("canvas");
      var ctx = canvas.getContext("2d");
      ctx.fillStyle = fontData[0]
      ctx.font = fontData[3]
      ctx.textAlign = fontData[4]
      ctx.fillText(name, fontData[1],fontData[2]);
    },
    changeTextNew(name,fontData,clear,canvasID){
      this.clearCanvasNew(canvasID,clear)
      var canvas = document.getElementById(canvasID);
      var ctx = canvas.getContext("2d");
      ctx.fillStyle = fontData[0]
      ctx.font = fontData[3]
      ctx.textAlign = fontData[4]
      ctx.fillText(name, fontData[1],fontData[2]);
    },
    clearCanvas(data){
      var canvas = document.getElementById("canvas");
      var ctx = canvas.getContext("2d");
      ctx.fillStyle = data[0]
      ctx.fillRect(data[1],data[2],data[3],data[4])

    },
    clearCanvasNew(canvasID,data){
      var canvas = document.getElementById(canvasID);
      var ctx = canvas.getContext("2d");
      ctx.fillStyle = data[0]
      ctx.fillRect(data[1],data[2],data[3],data[4])

    },
    saveCanvas(){

      var canvas = document.getElementById("canvas");

      var dataURL
      //var newTab = window.open('about:blank','image from canvas');
      //newTab.document.write("<img src='" + dataURL + "' alt='from canvas'/>");
      const doc = new jsPDF("p", "px", [793.706,1122.52]);
      var x = 10
      var y = 10

      for(const i in this.savedLabel){
        this.etiquette = this.savedLabel[i]
        //this.etiquette["productName"]= i
        console.log(this.etiquette)
        if(i%14==0 && i!=0){
          console.log("newPage")
          doc.addPage()
          y=-135
          
        }
        if(i%2 ==0){
          if(i!=0){
            y+=145
            x-=324
          }
          
        }
        else{
          x+=324
        }
        this.changeCanvas()
        dataURL = canvas.toDataURL("image/PNG");
        doc.addImage(dataURL,"png", x, y,321, 0,'',"slow");

      }
      //doc.save("a4.pdf");
      //window.open(doc.output('datauristring'));
      doc.save("a4.pdf")
      this.changeCanvas()
    },
    drawAllNew(canvasID,label){
      for (const data in this.writeCanvasDict){
        this.changeTextNew(label[data],this.writeCanvasDict[data],this.clearCanvasDict[data],canvasID)
      }
    },
    drawAll(){
      for (const data in this.writeCanvasDict){
        this.changeText(this.etiquette[data],this.writeCanvasDict[data],this.clearCanvasDict[data])
      }
    },
    drawCanvasNew(canvasID,label){
      var canvas = document.getElementById(canvasID);
      var ctx = canvas.getContext("2d");
      if(label["labelTheme"]=="old"){
        if(label["mainColor"] == "" && label["secondColor"] == ""){
          label["mainColor"] = this.themesColor["old"]["mainColor"]
          label["secondColor"] = this.themesColor["old"]["secondColor"]
        }

        this.writeCanvasDict = {
          year:[label["mainColor"],4,24,"20px DinProMedium"],
          productName:[label["mainColor"],4,60,"bold 30px DinProMedium","left"],
          reference:[label["mainColor"],582,24,"bold 20px DinProMedium","right"],
          description:[label["mainColor"],4,100,"30px DinProMedium","left"],
          thirdDescription:[label["mainColor"],4,130,"18px DinProLight","left"],
          secondDescription:[label["mainColor"],4,170,"18px DinProLight","left"],
          currency:[label["secondColor"],266,222,"30px DinProMedium","left"],
          price:[label["secondColor"],252,224,"bold 40px DinProMedium","right"],
          topRightSquare:[label["secondColor"],316,202," 24px DinProMedium","left"],
          bottomRightSquare:[label["secondColor"],316,236," 24px DinProMedium","left"]
        }
        this.clearCanvasDict = {
          year:[label["secondColor"],0,0,100,36],
          productName:[label["secondColor"],0,30,588,40],
          reference:[label["secondColor"],100,0,588,36],
          description:[label["secondColor"],0,70,588,40],
          thirdDescription:[label["secondColor"],0,110,588,30],
          secondDescription:[label["secondColor"],0,146,588,30],
          currency:[label["mainColor"],260,180,30,60],
          price:[label["mainColor"],0,180,260,60],
          topRightSquare:[label["mainColor"],304,178,290,28],
          bottomRightSquare:[label["mainColor"],304,214,290,28]
        }
        canvas.width=588
        canvas.height=260
        ctx.fillStyle = label["secondColor"];
        ctx.fillRect(0,0, canvas.width,canvas.height)
        ctx.fillStyle = label["mainColor"]
        ctx.fillRect(0,176,296,68)
        ctx.fillRect(300,176,288,32)
        ctx.fillRect(300,212,288,32)
      }
      else if(label["labelTheme"]=="new"){
        if(label["mainColor"] == "" && label["secondColor"] == ""){
          label["mainColor"] = this.themesColor["new"]["mainColor"]
          label["secondColor"] = this.themesColor["new"]["secondColor"]
        }        


        this.writeCanvasDict = {
          year:[label["secondColor"],4,24,"20px MyriadPro-Regular","left"],
          reference:[label["secondColor"],582,24,"bold 16px MyriadPro-Regular","right"],
          description:[label["secondColor"],294,28,"24px MyriadPro-Regular","center"],
          productName:[label["secondColor"],4,62,"bold 30px MyriadPro-Regular","left"],
          currency:[label["mainColor"],400,226,"bold 80px MyriadPro-Regular","left"],
          price:[label["mainColor"],386,226,"bold 80px MyriadPro-Regular","right"],
          topRightSquare:[label["mainColor"],464,184,"bold 20px MyriadPro-Regular","left"],
          bottomRightSquare:[label["mainColor"],464,228,"bold 20px MyriadPro-Regular","left"],
        }
        //#112655
        this.clearCanvasDict = {
          year:[label["mainColor"],0,0,116,36],
          reference:[label["mainColor"],470,0,120,36],
          description:[label["mainColor"],120,0,348,36],
          productName:[label["mainColor"],0,36,canvas.width,40],
          currency:[label["secondColor"],390,160,60,80],
          price:[label["secondColor"],20,160,372,80],
          topRightSquare:[label["secondColor"],450,166,118,26],
          bottomRightSquare:[label["secondColor"],450,210,118,26],
        }
        canvas.width=588
        canvas.height=260
        ctx.fillStyle = label["mainColor"];
        ctx.fillRect(0,0, canvas.width,canvas.height)
        ctx.fillStyle = label["secondColor"];
        ctx.fillRect(20,160, canvas.width-40,80)
      }
    },
    drawCanvas(name){
      var canvas = document.getElementById("canvas");
      var ctx = canvas.getContext("2d");
      if(name=="old"){
        this.etiquette["mainColor"] = this.themesColor["old"]["mainColor"]
        this.etiquette["secondColor"] = this.themesColor["old"]["secondColor"]
        this.writeCanvasDict = {
          year:[this.etiquette["mainColor"],4,24,"20px DinProMedium"],
          productName:[this.etiquette["mainColor"],4,60,"bold 30px DinProMedium","left"],
          reference:[this.etiquette["mainColor"],582,24,"bold 20px DinProMedium","right"],
          description:[this.etiquette["mainColor"],4,100,"30px DinProMedium","left"],
          thirdDescription:[this.etiquette["mainColor"],4,130,"18px DinProLight","left"],
          secondDescription:[this.etiquette["mainColor"],4,170,"18px DinProLight","left"],
          currency:[this.etiquette["secondColor"],266,222,"30px DinProMedium","left"],
          price:[this.etiquette["secondColor"],252,224,"bold 40px DinProMedium","right"],
          topRightSquare:[this.etiquette["secondColor"],316,202," 24px DinProMedium","left"],
          bottomRightSquare:[this.etiquette["secondColor"],316,236," 24px DinProMedium","left"]
        }
        this.clearCanvasDict = {
          year:[this.etiquette["secondColor"],0,0,100,36],
          productName:[this.etiquette["secondColor"],0,30,588,40],
          reference:[this.etiquette["secondColor"],100,0,588,36],
          description:[this.etiquette["secondColor"],0,70,588,40],
          thirdDescription:[this.etiquette["secondColor"],0,110,588,30],
          secondDescription:[this.etiquette["secondColor"],0,146,588,30],
          currency:[this.etiquette["mainColor"],260,180,30,60],
          price:[this.etiquette["mainColor"],0,180,260,60],
          topRightSquare:[this.etiquette["mainColor"],304,178,290,28],
          bottomRightSquare:[this.etiquette["mainColor"],304,214,290,28]
        }
        canvas.width=588
        canvas.height=260
        ctx.fillStyle = this.etiquette["secondColor"];
        ctx.fillRect(0,0, canvas.width,canvas.height)
        ctx.fillStyle = this.etiquette["mainColor"]
        ctx.fillRect(0,176,296,68)
        ctx.fillRect(300,176,288,32)
        ctx.fillRect(300,212,288,32)
      }
      else if(name=="new"){
        this.etiquette["mainColor"] = this.themesColor["new"]["mainColor"]
        this.etiquette["secondColor"] = this.themesColor["new"]["secondColor"]

        this.writeCanvasDict = {
          year:[this.etiquette["secondColor"],4,24,"20px MyriadPro-Regular","left"],
          reference:[this.etiquette["secondColor"],582,24,"bold 16px MyriadPro-Regular","right"],
          description:[this.etiquette["secondColor"],294,28,"24px MyriadPro-Regular","center"],
          productName:[this.etiquette["secondColor"],4,62,"bold 30px MyriadPro-Regular","left"],
          currency:[this.etiquette["mainColor"],400,226,"bold 80px MyriadPro-Regular","left"],
          price:[this.etiquette["mainColor"],386,226,"bold 80px MyriadPro-Regular","right"],
          topRightSquare:[this.etiquette["mainColor"],464,184,"bold 20px MyriadPro-Regular","left"],
          bottomRightSquare:[this.etiquette["mainColor"],464,228,"bold 20px MyriadPro-Regular","left"],
        }
        //#112655
        this.clearCanvasDict = {
          year:[this.etiquette["mainColor"],0,0,116,36],
          reference:[this.etiquette["mainColor"],470,0,120,36],
          description:[this.etiquette["mainColor"],120,0,348,36],
          productName:[this.etiquette["mainColor"],0,36,canvas.width,40],
          currency:[this.etiquette["secondColor"],390,160,60,80],
          price:[this.etiquette["secondColor"],20,160,372,80],
          topRightSquare:[this.etiquette["secondColor"],450,166,118,26],
          bottomRightSquare:[this.etiquette["secondColor"],450,210,118,26],
        }
        canvas.width=588
        canvas.height=260
        ctx.fillStyle = this.etiquette["mainColor"];
        ctx.fillRect(0,0, canvas.width,canvas.height)
        ctx.fillStyle = this.etiquette["secondColor"];
        ctx.fillRect(20,160, canvas.width-40,80)
      }
    },
    changeCanvas(){
      this.drawCanvas(this.etiquette["labelTheme"])
      this.drawAll()
    },
    nextLabel(){
      this.savedLabel.push(JSON.parse(JSON.stringify(this.etiquette)))
      this.cleanLabel()
      this.changeCanvas()
    },
    cleanLabel(){
      for(var i in this.labelCheckbox){
        if(this.labelCheckbox[i]== false){
          this.etiquette[i]=""
        }
      }
      this.themesColor[this.etiquette["labelTheme"]]["mainColor"] =this.etiquette['mainColor']
      this.themesColor[this.etiquette["labelTheme"]]["secondColor"] =this.etiquette['secondColor']

    },
    drawEditedCanvas(index){
      console.log(index)
      this.drawCanvasNew(this.savedLabel[index],"canvas"+index)
      this.drawAllNew(this.savedLabel[index],"canvas"+index)
    },
    dragover(event) {
      event.preventDefault();
      // Add some visual fluff to show the user can drop its files
      if (!event.currentTarget.classList.contains('bg-green-300')) {
        event.currentTarget.classList.add('bg-green-300');
      }
    },
    dragleave(event) {
      // Clean up
      event.currentTarget.classList.remove('bg-green-300');
    },
    drop(event) {
      event.preventDefault();
      var csvFile = event.dataTransfer.files;
      this.uploadFile(csvFile[0]); // Trigger the onChange event manually
      // Clean up
      event.currentTarget.classList.remove('bg-green-300');
    },
    onSendCSV(event){
      console.log(event.target.files)
      this.uploadFile(event.target.files[0])
      event.target.value = ""
    },
    uploadFile(csvFile){
      if(csvFile.type == "text/csv"){
        Papa.parse(csvFile, {header:true,
                                           complete: (results) =>{console.log("yo");
                                                                 console.log(results["data"])
                                                                 for(var i in results["data"]){
                                                                   if(results["data"][i]["labelTheme"]== "" || results["data"][i]["labelTheme"]== undefined){
                                                                     results["data"][i]["labelTheme"] = this.etiquette["labelTheme"]
                                                                   }
                                                                   if(results["data"][i]["mainColor"]== "" || results["data"][i]["mainColor"]== undefined){
                                                                     results["data"][i]["mainColor"] = this.themesColor[results["data"][i]["labelTheme"]]["mainColor"]
                                                                   }
                                                                   if(results["data"][i]["secondColor"]== "" || results["data"][i]["secondColor"]== undefined){
                                                                     results["data"][i]["secondColor"] = this.themesColor[results["data"][i]["labelTheme"]]["secondColor"]
                                                                   }
                                                                   this.savedLabel.push(results["data"][i])
                                                                 }}})
      }
      else{
        console.log("NOT VALID")
      }
    }
  }
}

</script>

