<template>
  <div v-bind:class='canvasClass+" position-relative"'>
    <div  v-if="index != -1" class="buttonDiv">
        <button class="btn btn-success btnCanvas" data-bs-toggle="collapse" type="button" v-bind:data-bs-target="'#expand'+index" aria-expanded="false" v-bind:aria-controls="'expand'+index">Edit</button>
        <button class="btn btn-danger btnCanvas" @click="$parent.$parent.deleteLabel(index)">Delete</button>
    </div>
    <canvas class="border rounded canvasWidth " v-bind:id="canvasID" width="588" height="260" @click="test"></canvas>
  </div>

</template>

<script>
export default {
  name: 'CanvasComponent',
  props: {
    canvasID: String,
    index: Number,
    canvasClass: String
  },
  mounted(){
    console.log("remount")
    this.$parent.$parent.redraw(this.canvasID,this.index)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .canvasWidth{
    width: 100%
  }
  .hover-overlay:hover canvas{
    -webkit-filter: blur(1.7px);
  }
  .hover-overlay:hover .buttonDiv{
    display: block;
    -webkit-filter: blur(0px);
  }
  .buttonDiv{
    z-index:1000;
    display:none;
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    right:0;
    bottom:0;
    margin-bottom:10px;
    text-align: center;
  }
  .btnCanvas{
    margin:10px;
  }
</style>
